import React, { Component } from 'react';

import HomeSeite from './pages/home';
import KontaktSeite from './pages/kontakt';

import {BrowserRouter as Router, Route, Switch} from 'react-router-dom';
import SeiteNichtGefunden from './pages/seiteNichtGefunden';
import Login from './pages/login';
import Impressum from './pages/impressum';
import DetailSeite from './pages/detail';
import SucheSeite from './pages/suche';
import KategorieSuchSeite from './pages/kategorieSuche';
import neuesRezeptSeite from './pages/neuesRezept';


/**
 * KOMMENTAR
 * + [Allgemein] Client Ordner ist Frontend; Der Hauptordner ist Backend
 * + [Allgemein] Im Backend existiert server.js -> eigenes Kommentarfeld
 * + Nach dem return darf es nur den einen <div> Tag geben, der bis zum Ende geht.
 *   Es darf nichts danach kommen.
 * + Beim Uebertragen des HTML-Codes muss beachtet werden: "class" wird zu "className".
 *   Bilder brauchen immer einen alt-tag. Und jeder Tag muss richtig geschlossen werden
 *   (ist im HTML Code oft nicht der Fall)
 * + Seiten sollten dann aus dem pages Ordner geladen werden.
 *   Es kann sein, dass dort nicht <a href> funktioniert, sonders es anders gelöst werden muss.
 * + Anscheinend darf es keine Kommentare in dem HTML-Code geben.
 * + Am besten den HTML-Code per Hand abschreiben und gleich schoen machen.
 *   Kopieren hat ein komsiches Format und hat oft Fehler mitgebracht, die nicht direkt gesehen
 *   wurden.
 * + Components wie <RezeptUebersicht /> sind genauso in den Code zu schreiben. Die feste Struktur
 *   von Components ist in RezeptUebersicht zu finden.
 * + Benutzer und Customer sind TestSpielerein und müssen nicht beachtet werden 
 */ 

/**
 * TODO FUER DAS GESAMTPROJEKT
 * + Das Icon muss aus public/index.html ausgetauscht werden gegen unseres.
 * + Frontend muss spaeter noch ordentlich vom Backend getrennt werden, um bei Zeit.co
 *   hochgeladen werden zu koennen (Wie auch immer das geloest wird).
 * + Login muss implementiert werden
 * + Rezepterstellung muss implementiert werden
 * + In Impressum den Fehler "The tag .. is unrecognized in this browser. If you meant to render 
 *   a React component, start its name with an uppercase letter." beheben.
 */

class App extends Component {
  render() {
    return (
      <Router>
        <Switch>
          <Route path="/" component={HomeSeite} exact /> 
          <Route path="/kontakt" component={KontaktSeite} />
          <Route path="/login" component={Login} />
          <Route path="/impressum" component={Impressum}/>
          <Route path="/detail/:rid" component={DetailSeite}/>
          <Route path="/suche" component={SucheSeite} />
          <Route path="/neuesrezept" component={neuesRezeptSeite} />
          <Route path="/kategoriesuche/:kid" component={KategorieSuchSeite} />
          <Route component={SeiteNichtGefunden} />
        </Switch>
      </Router>
    );
  }
}

export default App;



